import { Component, OnInit } from '@angular/core';
import { Notifications } from 'app/shared/models';
import { NotificationService } from 'app/shared/services/api/notification.service';
import { SwalService } from 'app/shared/services';

@Component({
  selector: 'app-notifications-list',
  templateUrl: './notifications-list.component.html',
  styleUrls: ['./notifications-list.component.scss']
})
export class NotificationsListComponent implements OnInit {

// main object
  notification: Notifications


  notificationList: Notifications[]=[];

  editObj :Notifications = new Notifications();
  index : number
  data=[];
  pagenumber=0;
  pageTitle=1;
  constructor(
    private notificationServices: NotificationService ,
    private swalService :SwalService
  ) { }

  ngOnInit(): void {
    this.notification = new Notifications();
    this.getAllData();

  }
  
  
  //post
  create(){
    this.notificationServices.create(this.notification).subscribe(
      res=> {
        this.cancel();
        this.getAllData();
        this.swalService.Notifier('تم  الحفظ بنجاح');

      }, err => {
        let errorMessage = err.message || 'حدث خطأ قي استلام البيانات';
        this.swalService.NotifierError(errorMessage)
      })
  }
  cancel(){
    this.notification.text ='';
    this.notification.id =0;
  }


   // get all data of category
 getAllData(){
  this.notificationServices.getAll('' ,'1','100').subscribe(res =>{
      this.data = []
      let a  = (res.result.length /100)+1;
      
      for (let index = 1; index <= a; index++) {
         this.data.push(index)     }
      this.notificationList = res.result;
    
  })

}


 //fill
 fill(prop: Notifications) {
  this.notification.id = prop.id;
  this.notification.text = prop.text;

}

open(){
  this.cancel();
  }


  delete( index: number ) {
    this.editObj = {...this.notificationList[index]}
    this.index = index;
     this.swalService.showRemoveConfirmation(index).then(
       result => {
         if (result.value) {
           this.notificationServices.delete(this.editObj.id).subscribe(
         
             res => {
               this.swalService.Notifier('تم مسح البيانات بنجاح ');
               this.cancel();
               this.getAllData();
    
             },
             err => {
               let errorMessage = err.message || ' خطآ في مسح البيانات  ';
               this.swalService.NotifierError(errorMessage)
             }
           )
         }
       }
     );
    }
    
    saveupdate(){
    this.update();
    }
    
    update( ) {
    this.notificationServices.update( this.notification.id,this.notification).subscribe(
      res => {
        this.swalService.Notifier('تم حفظ التعديلات بنجاح ');
         this.cancel();
        this.getAllData();
      },
      err => {
        let errorMessage = err.data || ' حدث خطأ اثناء الحفظ .. من فضلك حاول مرة آخري    ';
        this.swalService.NotifierError(errorMessage)
    
      }
    )
    }

}
